.avatar{font-size:.8rem;height:1.6rem;width:1.6rem;background:#5755d9;border-radius:50%;color:hsla(0,0%,100%,.85);display:inline-block;font-weight:300;line-height:1.25;margin:0;position:relative;vertical-align:middle}.avatar.avatar-xs{font-size:.4rem;height:.8rem;width:.8rem}.avatar.avatar-sm{font-size:.6rem;height:1.2rem;width:1.2rem}.avatar.avatar-lg{font-size:1.2rem;height:2.4rem;width:2.4rem}.avatar.avatar-xl{font-size:1.6rem;height:3.2rem;width:3.2rem}.avatar img{border-radius:50%;height:100%;position:relative;width:100%;z-index:1}.avatar .avatar-icon,.avatar .avatar-presence{background:#fff;bottom:14.64%;height:50%;padding:.1rem;position:absolute;right:14.64%;transform:translate(50%, 50%);width:50%;z-index:2}.avatar .avatar-presence{background:rgb(188.1315789474,195.2236842105,206.3684210526);box-shadow:0 0 0 .1rem #fff;border-radius:50%;height:.5em;width:.5em}.avatar .avatar-presence.online{background:#32b643}.avatar .avatar-presence.busy{background:#e85600}.avatar .avatar-presence.away{background:#ffb700}.avatar[data-initial]::before{color:currentColor;content:attr(data-initial);left:50%;position:absolute;top:50%;transform:translate(-50%, -50%);z-index:1}